    background: #000;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-perspective: 500px;
            perspective: 500px;
  }
  .Home{
    height: 100vh !important;
    width: 100vw !important;
  }
  .SpaceCraft{
    z-index: -1000;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -50px;
    width: 100px;
    height: 100px;
  }
